import { Component, OnInit } from '@angular/core';
import { ReviewsService } from 'src/app/services/reviews/reviews.service';
import { Review } from 'src/app/services/reviews/review';

@Component({
  selector: 'app-review-informer',
  templateUrl: './review-informer.component.html',
  styleUrls: ['./review-informer.component.scss']
})
export class ReviewInformerComponent implements OnInit {

  items: Review[] = null;

  constructor(private reviewsService: ReviewsService) { }

  ngOnInit() {
    this.reviewsService.getItems({top: 6})
      .subscribe((items: Review[]) => this.items = items);
  }

}
