import { Injectable } from '@angular/core';
import { Magazine } from './magazine';
import { ListItemsService } from '../list-items-service';
import * as moment from 'moment';
import { PortalService } from '../portal/portal.service';

@Injectable({
  providedIn: 'root'
})
export class MagazinesService extends ListItemsService<Magazine> {

  constructor(private sharepointService: PortalService) {
    super();
  }

  getListTitle(): string {
    return 'Наши издания - Журналы';
  }

  getListRelativePath(): string {
    return 'lists/slMagazines';
  }

  getSelect(): string {
    return 'Id,Title,slMagazinelUrl,slImageUrl,slReleaseDate,slNumber';
  }

  getExpand(): string {
    return null;
  }

  getOrderBy(): [string, boolean][] {
    return [['slReleaseDate', false]];
  }

  getTop(): number {
    return 1;
  }

  convertListItemToEntity(item: any): Magazine {
    const entity: Magazine = {
      id: item.Id,
      title: item.Title,
      number: item.slNumber,
      imageUrl: item.slImageUrl,
      magazinelUrl: item.slMagazinelUrl,
      releaseDate: item.slReleaseDate && new Date(item.slReleaseDate)
    };
    return entity;
  }

  convertEntityToListItem(entity: Magazine) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): Magazine[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }
}
