import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CompanyComponent } from './components/company/company.component';
import { GroupsResolver } from './services/security/groups.resolver';
import { CurrentUserIdResolver } from './services/security/current-user-id.resolver';

const routes: Routes = [
  {
    path: '',
    component: CompanyComponent,
    resolve: {
      groups: GroupsResolver,
      userId: CurrentUserIdResolver
    },
    data: { animation: 'fadeAnimation', title: 'Главная страница' }
  }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    providers: [
      CurrentUserIdResolver,
      GroupsResolver
    ],
    exports: [RouterModule]
})
export class CompanyRoutingModule { }
