import { Component, OnInit, NgZone } from '@angular/core';
import { Router } from '@angular/router';

import { Banner } from 'src/app/services/banners/banner';
import { PortalService } from 'src/app/services/portal/portal.service';
import { BannersService } from 'src/app/services/banners/banners.service';

@Component({
  selector: 'app-main-carousel',
  templateUrl: './main-carousel.component.html',
  styleUrls: ['./main-carousel.component.scss']
})

export class MainCarouselComponent implements OnInit {

  public banners: Banner[] = [];
  public carouselIsUp: Boolean = false;
  public currentSlide = 0;

  constructor(private sharepointService: PortalService,
    private bannersService: BannersService,
    private zone: NgZone,
    private router: Router) { }

  ngOnInit() {
    this.load();
  }

  private load() {
    this.bannersService.getItems({ filter: 'slShow eq 1' })
      .subscribe(banners => {
        this.currentSlide = 0;
        this.banners = banners;
      });
  }

  public carouselToFront(val) {
    this.carouselIsUp = val;
  }

  create() {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + '/Lists/slBanners/NewForm.aspx?Source='
      + encodeURIComponent(window.location.href);
  }

  edit(item: any) {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/Lists/slBanners/EditForm.aspx?ID=${item.id}&Source=`
      + encodeURIComponent(window.location.href);
  }

  remove(item: any) {
    this.bannersService.recycleItem(item)
      .subscribe(() => this.load());
  }

  public prepareLink(link: string) {
    return link && link.replace(/\(/gi, '\\(').replace(/\)/gi, '\\)') || null;
  }

  public goTo(item: Banner) {
    this.zone.run(() => {
      const link = item.url;
      if (window.location.href.indexOf('/PortalPages/') > 0
        && link.indexOf('/PortalPages/') >= 0
        && link.indexOf('#') >= 0) {
        this.router.navigate([link.split('#')[1]]);
      } else {
        window.location.href = link;
      }
    });
  }
}
