import { Component, OnInit } from '@angular/core';

import { Magazine } from 'src/app/services/magazines/magazine';
import { Newspaper } from 'src/app/services/newspapers/newspaper';
import { MagazinesService } from 'src/app/services/magazines/magazines.service';
import { NewspapersService } from 'src/app/services/newspapers/newspapers.service';
import { ImagesService } from 'src/app/services/images/images.service';

@Component({
  selector: 'app-our-publications-informer',
  templateUrl: './our-publications-informer.component.html',
  styleUrls: ['./our-publications-informer.component.scss']
})
export class OurPublicationsInformerComponent implements OnInit {

  public magazine: Magazine = null;
  public newspaper: Newspaper = null;

  constructor(private magazinesService: MagazinesService,
    private newspapersService: NewspapersService,
    private imagesService: ImagesService) { }

  ngOnInit() {
    this.magazinesService.getItems({orderBy: [['slReleaseDate', false]], top: 1})
      .subscribe((items: Magazine[]) => {
        this.magazine = items.length > 0 ? items[0] : null;
      });
    this.newspapersService.getItems({orderBy: [['slReleaseDate', false]], top: 1})
      .subscribe((items: Newspaper[]) => {
        this.newspaper = items.length > 0 ? items[0] : null;
      });
  }

  public prepareLink(link: string) {
    return this.imagesService.prepareBackgroundUrl(link);
  }
}
