import { Injectable } from '@angular/core';

import * as moment from 'moment';

import { Newspaper } from './newspaper';
import { ListItemsService } from '../list-items-service';

@Injectable({
  providedIn: 'root'
})
export class NewspapersService extends ListItemsService<Newspaper> {

  getListTitle(): string {
    return 'Наши издания - Газеты';
  }

  getListRelativePath(): string {
    return 'lists/slNewspapers';
  }

  getSelect(): string {
    return 'Id,Title,slNewspaperUrl,slImageUrl,slReleaseDate,slNumber';
  }

  getExpand(): string {
    return null;
  }

  getOrderBy(): [string, boolean][] {
    return [['slReleaseDate', false]];
  }

  getTop(): number {
    return 1;
  }

  convertListItemToEntity(item: any): Newspaper {
    const entity: Newspaper = {
      id: item.Id,
      title: item.Title,
      number: item.slNumber,
      imageUrl: item.slImageUrl,
      newspaperUrl: item.slNewspaperUrl,
      releaseDate: item.slReleaseDate && new Date(item.slReleaseDate)
    };
    return entity;
  }

  convertEntityToListItem(entity: Newspaper) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): Newspaper[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }
}
