import { Component, OnInit, ViewChild, ElementRef, NgZone } from '@angular/core';

import { PortalService } from 'src/app/services/portal/portal.service';
import { FeedbackService } from 'src/app/services/feedback/feedback.service';

@Component({
  selector: 'app-feedback-informer',
  templateUrl: './feedback-informer.component.html',
  styleUrls: ['./feedback-informer.component.scss']
})
export class FeedbackInformerComponent implements OnInit {

  @ViewChild('descriptionText') private descriptionText: ElementRef;
  @ViewChild('buttonSend') private buttonSend: ElementRef;

  constructor(public feedbackService: FeedbackService,
    private sharepointService: PortalService,
    private zone: NgZone) { }

  feedbackStatusUpdate(status: Boolean) {
    this.zone.run(() => {
      if (status) {
        this.descriptionText.nativeElement.innerText = 'Спасибо! Ваш вопрос успешно отправлен. Мы ответим в скором времени';
        this.buttonSend.nativeElement.value = 'Написать еще';
      }
      if (!status) {
        this.descriptionText.nativeElement.innerText = 'Ваш вопрос не удалось отправить получателю.';
      }
    });
  }

  ngOnInit() {
    this.feedbackService.emailSendEvent.subscribe(status => {
      this.feedbackStatusUpdate(status);
    });
  }

  create() {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + '/Lists/slQuestions/NewForm.aspx?Source='
      + encodeURIComponent(window.location.href);
  }

  edit() {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/Lists/slQuestions`;
  }
}
