import { Component, OnInit } from '@angular/core';

import { PortalService } from 'src/app/services/portal/portal.service';
import { KeyEvent } from 'src/app/services/key-events/key-event';
import { KeyEventsService } from 'src/app/services/key-events/key-events.service';

@Component({
  selector: 'app-key-events',
  templateUrl: './key-events.component.html',
  styleUrls: ['./key-events.component.scss']
})
export class KeyEventsComponent implements OnInit {

  public keyEvents: KeyEvent[] = [];
  public currentSlide = 0;

  constructor(private sharepointService: PortalService,
    private keyeventsService: KeyEventsService) {}

  ngOnInit() {
    this.load();
  }

  load() {
    const currentDate = new Date().toISOString();
    this.keyeventsService.getItems({
      filter: `(slShowDate le datetime'${currentDate}') and (slArchiveDate gt datetime'${currentDate}')`,
      top: 3
    }).subscribe(keyEvents => {
      this.currentSlide = 0;
      this.keyEvents = keyEvents;
    });
  }

  create() {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + '/Lists/slKeyEvents/NewForm.aspx?Source='
      + encodeURIComponent(window.location.href);
  }

  edit(item: any) {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/Lists/slKeyEvents/EditForm.aspx?ID=${item.id}&Source=`
      + encodeURIComponent(window.location.href);
  }

  remove(item: any) {
    this.keyeventsService.recycleItem(item)
      .subscribe(() => this.load());
  }
}
