import { Component, OnInit } from '@angular/core';
import { flatMap } from 'rxjs/operators';
import { of } from 'rxjs';

import { CookieService } from 'ngx-cookie-service';

import { PortalService } from 'src/app/services/portal/portal.service';
import { PhotoLibraryService } from 'src/app/services/photo-library/photo-library.service';
import { VideosService } from 'src/app/services/videos/videos.service';
import { ImagesService } from 'src/app/services/images/images.service';
import { PhotoLibrary } from 'src/app/services/photo-library/photo-library';
import { contentTypes } from 'src/app/constants/content-types';
import { Video } from 'src/app/services/videos/video';

@Component({
  selector: 'app-media-carousel',
  templateUrl: './media-carousel.component.html',
  styleUrls: ['./media-carousel.component.scss']
})
export class MediaCarouselComponent implements OnInit {

  public albums: any[] = [];
  public carouselIsUp: Boolean = false;
  public currentSlide = 0;

  constructor(
    private sharepointService: PortalService,
    private photoLibraryService: PhotoLibraryService,
    private videosService: VideosService,
    private imagesService: ImagesService,
    private cookieService: CookieService) { }

  ngOnInit() {
    this.load();
  }

  load() {
    this.photoLibraryService.getItems({filter: `ContentType eq '${encodeURI(contentTypes.album)}'`, top: 1}).pipe(
      flatMap((album: PhotoLibrary[]) => {
        return this.photoLibraryService.getFiles(album[0].id);
      })
    ).subscribe({
      next: (value: any) => {
        for (let i = 0; i < value.length; i++) {
          this.albums.push(value[i]);
        }
      },
      complete: () => {
        this.videosService.getRandomItems({top: 10}).pipe(
          flatMap((album: Video[]) => {
            return of(album);
          })
          ).subscribe({
          next: (value: any) => {
            for (let i = 0; i < value.length; i++) {
              this.albums.push(value[i]);
            }
          },
          complete: () => {
            this.albums.sort(() => 0.5 - Math.random());
            this.albums.length = 10;
          }
        });
      }
    });
  }

  create() {
    this.cookieService.set('splnu', '0', 1, '/');
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/slPhotoLibrary/Forms/Upload.aspx?ContentTypeId=0x012000BFB863A2672645ABBEA198EF8334172D&Source=`
      + encodeURIComponent(window.location.href);
  }

  edit(item: any) {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/slPhotoLibrary/Forms/EditForm.aspx?ID=${item.id}&Source=`
      + encodeURIComponent(window.location.href);
  }

  public carouselToFront(val) {
    this.carouselIsUp = val;
  }

  public prepareLink(link: string) {
    return this.imagesService.prepareBackgroundUrl(link);
  }

  public getFileNameAndExtension(path) {
    const str = path
      .split('\\')
      .pop()
      .split('/')
      .pop();
    const ext = str.substr((Math.max(0, str.lastIndexOf('.')) || Infinity) + 1);
    const name = str.replace('.' + ext, '');
    return {
      ext,
      name,
      full: `${name}.${ext}`
    };
  }
}
