import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { Employee } from 'src/app/services/employees/employee';
import { EmployeesService } from 'src/app/services/employees/employees.service';

@Component({
  selector: 'app-birthdays-informer',
  templateUrl: './birthdays-informer.component.html',
  styleUrls: ['./birthdays-informer.component.scss']
})
export class BirthdaysInformerComponent implements OnInit {
  public employees: Employee[] = null;
  public currentDay: number;

  constructor(private route: ActivatedRoute,
    private employeesService: EmployeesService) {
    this.currentDay = new Date().getDate();
  }

  ngOnInit() {
    this.employees = this.route.snapshot.data.birthdays;
    const year = new Date().getFullYear();
    const month = new Date().getMonth() + 1;
    const start = new Date().getDate();
    const finish = new Date(year, month, 0).getDate();
    this.employeesService.getItems({
      filter: `slBirthdayMonth eq ${month} and slBirthdayDay ge ${start} and slBirthdayDay le ${finish}`,
      orderBy: [['slBirthdayDay', true], ['slFullName', true]],
      top: 5000
    }).subscribe(birthdays => this.employees = birthdays);
  }
}
