import { Component, OnInit } from '@angular/core';

import * as moment from 'moment';

import { Employee } from 'src/app/services/employees/employee';
import { EmployeesService } from 'src/app/services/employees/employees.service';

@Component({
  selector: 'app-new-employees-informer',
  templateUrl: './new-employees-informer.component.html',
  styleUrls: ['./new-employees-informer.component.scss']
})
export class NewEmployeesInformerComponent implements OnInit {

  currentSlide = 0;
  prevSlide: number = null;
  nextSlide: number = null;

  public employees: Employee[] = [];

  constructor(private employeeService: EmployeesService) { }

  ngOnInit() {
    // this.employees = this.route.snapshot.data.newEmployees;
    const start = moment().startOf('quarter').toISOString();
    this.employeeService.getItems({
      filter: `slStartWorkDate ge datetime'${start}'`,
      orderBy: [['slStartWorkDate', true]],
      top: 100
    }).subscribe(employees => {
      this.employees = employees;
      if (this.employees) {
        if (this.employees.length > 0) {
          this.currentSlide = this.employees.length - 1;
        }
        if (this.employees.length > 1) {
          this.prevSlide = this.employees.length - 2;
        }
      }
    });
  }
}
