import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { flatMap } from 'rxjs/operators';
import { of } from 'rxjs';

import { Survey } from 'src/app/services/surveys/survey';
import { SurveysService } from 'src/app/services/surveys/surveys.service';
import { PortalService } from 'src/app/services/portal/portal.service';

@Component({
  selector: 'app-polls-informer',
  templateUrl: './polls-informer.component.html',
  styleUrls: ['./polls-informer.component.scss']
})
export class PollsInformerComponent implements OnInit {

  public surveys: Survey[] = [];
  public userId: number;
  public carouselIsUp: Boolean = false;
  public currentSlide = 0;

  constructor(private surveysService: SurveysService,
    private route: ActivatedRoute,
    private sharepointService: PortalService) { }

  ngOnInit() {
    this.userId = this.route.snapshot.data.userId;
    this.load();
  }

  private load() {
    const xml = `<View><Query>
                  <Where>
                    <And>
                      <And>
                        <Leq>
                          <FieldRef Name='slNewDate' />
                          <Value IncludeTimeValue='TRUE' Type='DateTime'>${new Date().toISOString()}</Value>
                        </Leq>
                        <NotIncludes>
                          <FieldRef Name='LikedBy' LookupId='TRUE'/>
                          <Value Type='Integer'>${this.userId}</Value>
                        </NotIncludes>
                      </And>
                        <Or>
                          <Geq>
                              <FieldRef Name='slEndDate' />
                              <Value IncludeTimeValue='TRUE' Type='DateTime'>${new Date().toISOString()}</Value>
                          </Geq>
                          <IsNull>
                              <FieldRef Name='slEndDate' />
                          </IsNull>
                        </Or>
                    </And>
                  </Where>
                  <OrderBy>
                    <FieldRef Name='Created' Ascending='False' />
                  </OrderBy>
              </Query><RowLimit>3</RowLimit></View>`;
  }

  create() {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + '/Lists/slSurvey/NewForm.aspx?Source='
      + encodeURIComponent(window.location.href);
  }

  edit(item: any) {
    window.location.href = this.sharepointService.getCurrentUserContext().webServerRelativeUrl
      + `/Lists/slSurvey/EditForm.aspx?ID=${item.id}&Source=`
      + encodeURIComponent(window.location.href);
  }

  remove(item: any) {
    this.surveysService.recycleItem(item)
      .subscribe(() => {
        this.load();
      });
  }

  public carouselToFront(val) {
    this.carouselIsUp = val;
  }

  public goTo(survey: Survey) {
    window.open(survey.link, '_blank');
    this.addLike(survey);
  }

  public addLike(item: Survey): void {
    if (item.likedByIds.indexOf(this.userId) < 0) {
      this.surveysService.addLike({id: item.id}).subscribe(() => {
        item.likedByIds.push(this.userId);
         item.likesCount++;
      });
    }
  }
}
