import { Injectable } from '@angular/core';
import * as moment from 'moment';

import { KeyEvent } from './key-event';
import { ListItemsService } from '../list-items-service';
import { PortalService } from '../portal/portal.service';

@Injectable({
  providedIn: 'root'
})
export class KeyEventsService extends ListItemsService<KeyEvent>  {

  constructor(private sharepointService: PortalService) {
    super();
  }

  getListRelativePath(): string {
    return '';
  }

  getListTitle(): string {
    return 'Ключевые события';
  }

  getSelect(): string {
    return 'Id,Title,slEventDate,slShowDate,slTextEvent,slArchiveDate,Created';
  }

  getExpand(): string {
    return null;
  }

  getOrderBy(): [string, boolean][] {
    return [['slEventDate', true]];
  }

  getTop(): number {
    return 10;
  }

  convertListItemToEntity(item: any): KeyEvent {
    const keyEvent: KeyEvent = {
      id: item.Id,
      title: item.Title,
      eventDate: item.slEventDate && new Date(item.slEventDate),
      showDate: item.slShowDate && new Date(item.slShowDate),
      textEvent: item.slTextEvent,
      archiveDate: item.slArchiveDate && new Date(item.slArchiveDate)
    };
    if (keyEvent.eventDate != null) {
      keyEvent.eventDateTitle = moment(keyEvent.eventDate).calendar(null, {
          sameDay: '[Сегодня]',
          nextDay: '[Завтра]',
          nextWeek: 'DD MMMM YYYY',
          lastDay: '[Вчера]',
          lastWeek: 'DD MMMM YYYY',
          sameElse: 'DD MMMM YYYY'
      });
    }
    return keyEvent;
  }

  convertEntityToListItem(entity: KeyEvent) {
    throw new Error('Method not implemented.');
  }

  getMockItems(): KeyEvent[] {
    return [];
  }

  isMock(): boolean {
    return false;
  }

}
